<template>
  <div class="timer-body">
    <p>{{ time }}</p>
    <ul>
      <li><button @click="">Start</button></li>
      <li><button @click="">Stop</button></li>
    </ul>
  </div>
</template>

<script>
export default {
  data () {
    return {
      time: ''
    }
  },
  created: function() {

  },
  methods: {
    start: function() {
      window.setInterval(() => {
          this.time = Math.trunc((new Date()).getTime() / 1000);
      }, 1000)
    },
    stop: function() {
      window.clearInterval(this.time)
    }
  },
  beforeDestroy() {
    window.clearInterval(this.time)
  }
}
</script>



